import React from "react";
import GithubIcon from "./img/GitHub-Mark-64px.png";
import { NavLink } from "react-router-dom";
import AnchorLink from "react-anchor-link-smooth-scroll";

const Header = () => {
  return (
    <>
      <header className="header">
        <div className="header-wrapper">
          <div className="logo-wrap">
            <NavLink
              activeClassName="active"
              to="/"
              className="logo-wrapper non-select"
            >
              <div className="logo">
                <j>J</j>iei
              </div>
            </NavLink>
          </div>
          <nav className="nav">
            <ul className="list-wrapper">
              <NavLink
                activeClassName="active"
                to={"/About/"}
                className="underline non-select"
              >
                <li className="li-item button-text">About</li>
              </NavLink>
              <NavLink
                activeClassName="active"
                to={"/Works/"}
                className="underline non-select"
              >
                <li className="li-item button-text dropdown-menu">
                  Works
                  <div className="dropdown-list">
                    <ul>
                      <li className="dropdown-item">
                        <AnchorLink
                          href="#webSite"
                          offset="100"
                          className="button-text non-select"
                        >
                          Web site
                        </AnchorLink>
                      </li>
                      <li className="dropdown-item">
                        <AnchorLink
                          href="#logo"
                          offset="100"
                          className="button-text non-select"
                        >
                          Logo
                        </AnchorLink>
                      </li>
                      <li className="dropdown-item">
                        <AnchorLink
                          href="#illust"
                          offset="100"
                          className="button-text non-select"
                        >
                          Illust
                        </AnchorLink>
                      </li>
                      <li className="dropdown-item">
                        <AnchorLink
                          href="#lottie"
                          offset="100"
                          className="button-text non-select"
                        >
                          Lottie
                        </AnchorLink>
                      </li>
                    </ul>
                  </div>
                </li>
              </NavLink>
              <NavLink
                activeClassName="active"
                to={"/Contact/"}
                className="underline non-select"
              >
                <li className="li-item button-text">Contact</li>
              </NavLink>
            </ul>
          </nav>
          <div className="github-icon non-select">
            <a href="https://github.com/1203recruit" target={"blank"}>
              <img src={GithubIcon} />
            </a>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
